//
// Syntax-highlighting theme 'Zenburn' --
//

.highlight {
  overflow-x: auto;
  // @media (min-width: $small) and (max-width: $medium-wide) {
  //   width: 550px;
  // }
  pre {
    margin: 0;
    padding: 1em;
    width: 100%;
    color: #fdce93;
    background-color: #3f3f3f;
    // border-radius: $border-radius;
  }
  table td { padding: 5px; }
  table pre { margin: 0; }
}

.highlighttable {
  tr:hover > td,
  tr:hover > th {
    background: transparent;
  }
}

